.ecl-news-wrapper {
  display: flex;
  overflow-x: auto;

  ion-card {
    flex: 0 0 65%;
    height: 300px;
    display: flex;
    flex-direction: column;

    ion-img {
      height: 50%;
      object-fit: cover;
    }

    ion-card-header {
      padding: 12px 20px 4px 20px;
      ion-card-title {
        font-size: 1.2rem;
        ::ng-deep p {
          margin: 0;
        }
      }
    }
  }
}
ion-modal.modal-sheet::part(content) {
  overflow: visible;
  --overflow: visible;
}
